
.showRouter-enter-active {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
.showRouter-leave-active {
  -webkit-transition: all 0 ease;
  transition: all 0 ease;
}
.showRouter-enter, .showRouter-leave-active {
  -webkit-transform: translateX(-150px);
          transform: translateX(-150px);
  opacity: 0;
}
.music-list {
  padding-top: 4px;
  padding-left: 4px;
  padding-right: 4px;
  -webkit-box-flex: 8;
      -ms-flex: 8;
          flex: 8;
  overflow: auto;
}
.music-list .music-item + .music-item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.music-list .music-item {
    padding: 4px 6px 0 6px;
    position: relative;
    margin-bottom: 4px;
    border-radius: 5px;
    cursor: pointer;
    border: none;
}
.music-list .music-item .music-img {
      width: 50px;
      height: 50px;
      border-radius: 5px;
}
.music-list .music-item span.music-name {
      display: inline-block;
      width: 65%;
      vertical-align: top;
      margin-top: 15px;
      margin-left: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      cursor: pointer;
      padding-bottom: 20px;
}
.music-list .music-item span.del-icon {
      display: inline-block;
      position: absolute;
      right: 10px;
      top: 20px;
      width: 20px;
      height: 20px;
      background: url("./del.svg");
      background-size: contain;
      cursor: pointer;
}
.music-list .tips {
    text-align: center;
    margin: 12px auto;
    width: 200px;
    font-size: 80%;
    color: gray;
}



