
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html, body {
  width: 100%;
  height: 100%;
  overflow: hidden;
}
@media screen and (min-width: 769px) {
html, body {
    width: 460px;
    margin: 0 auto;
}
body {
    box-shadow: 0 0 30px gray;
}
}
a {
  position: relative;
  text-decoration: none;
  color: white;
  cursor: pointer;
}
a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: #3F51B5;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
a:hover::after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
.hover {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transition: .3s ease-in-out;
  transition: .3s ease-in-out;
}
.rotate {
  -webkit-animation: imgRotate 9s linear infinite;
          animation: imgRotate 9s linear infinite;
}
@-webkit-keyframes imgRotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
@keyframes imgRotate {
from {
    -webkit-transform: rotate(0);
            transform: rotate(0);
}
to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
}
}
.fade-enter-active {
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
}
.fade-leave-active {
  -webkit-transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
  transition: all 0.3s cubic-bezier(1, 0.5, 0.8, 1);
}
.fade-enter, .fade-leave-active {
  -webkit-transform: translateX(10px);
          transform: translateX(10px);
  opacity: 0;
}
.showIndex-enter-active {
  -webkit-transition: all .4s ease-out;
  transition: all .4s ease-out;
}
.showIndex-leave-active {
  -webkit-transition: all 0 ease;
  transition: all 0 ease;
}
.showIndex-enter, .showIndex-leave-active {
  -webkit-transform: translateY(350px);
          transform: translateY(350px);
  opacity: 0;
}
.show-enter-active {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}
.show-leave-active {
  -webkit-transition: all 0 ease-out;
  transition: all 0 ease-out;
}
.show-enter, .show-leave-active {
  -webkit-transform: translateX(-350px);
          transform: translateX(-350px);
  opacity: 0;
}
.down-enter-active {
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}
.down-leave-active {
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}
.down-enter, .down-leave-active {
  -webkit-transform: translateY(-250px);
          transform: translateY(-250px);
  opacity: 0;
}
#app, .index {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
}
#app div.search-page, .index div.search-page {
    position: absolute;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
    width: 100%;
    height: 100%;
    z-index: 1;
}
#app div.search-page .search-input, .index div.search-page .search-input {
      position: relative;
      -webkit-box-flex: 1.1;
          -ms-flex: 1.1;
              flex: 1.1;
      width: 100%;
      background-color: white;
      border: none;
}
#app div.search-page .search-input input, .index div.search-page .search-input input {
        width: 90%;
        height: 100%;
        padding: 0 55px 0 30px;
        outline: none;
        border: none;
        font-size: 1.2rem;
}
#app div.search-page .search-input i.icon-search, .index div.search-page .search-input i.icon-search {
        position: absolute;
        top: 9px;
        right: 55px;
        display: inline-block;
        width: 25px;
        height: 25px;
        background: url("./assets/search.svg") no-repeat;
        background-size: contain;
}
#app div.search-page .search-input span, .index div.search-page .search-input span {
        display: inline-block;
        position: absolute;
        right: 10px;
        top: 10px;
        width: 23px;
        height: 23px;
        background: url("./assets/close.svg") no-repeat;
        background-size: contain;
}
#app div.search-page .mask, .index div.search-page .mask {
      -webkit-box-flex: 15;
          -ms-flex: 15;
              flex: 15;
}



